* {
  box-sizing: border-box;
}
/* 
.App-start {

} */

.container {
  position: relative;
  width: 320px;
}

.winner {
  display: none;
  position: absolute;
  width: 320px;
  height: 320px;
  background-color:#000;
  top: 0; 
  left: 0;
  color: #000;
  text-align: center;
  font-style: italic;
  font-size: 30px;
  padding-top: 35%;   
}

.App_header {
  display: block;
  width: 310px;
  text-align: center;
  border: 2px solid #000;
}

.cell {
  display: inline-block;
  font-size: 30px;
  width: 100px;
  height: 100px;
  margin: 2px;
  border: 2px solid #f00;
  border-radius: 10px;
  cursor: pointer;
}

.btn {
  padding: 10px;
  display: block;
  color: #fff;
  background-color: #000;
  border: 2px solid #000;
  text-transform: uppercase;
  letter-spacing: 20px;
  cursor: pointer;
}

button:hover {
  color: #000;
  background-color: #fff;
}

.Reset_btn {
  width: 80%;
  margin: 10px 10%; 
}

.Start_btn {
  width: 300px;
  text-align: left;
  margin: 0;
}

.Start_btn:hover {
  text-align: right;
}
